<template>
  <div class="service-home-wrap">
    <div v-title>噢啦渠道商</div>
    <div class="service-home">

      <!--通知栏  一期暂时隐藏-->
      <div class="callboard" v-if="false">
        <img src="../../assects/images/order_notice.svg" class="notice-icon">
        <ul  class="info-list">
          <li class="info-item">【卢瑶瑶】捐赠的iPhone X于16:00送达，该订单…</li>
        </ul>
      </div>

      <!--渠道合作商简单信息栏-->
      <div class="partner-info">
        <img
          @click="changeCodeShowStatus"
          src="../../assects/images/channel_code.svg"
          class="code-icon">
        <!--一期暂时隐藏-->
        <div class="news-notice" v-if="false">
          <img src="../../assects/images/notice.svg">
          <p class="news-num">3</p>
        </div>
        <img src="http://placehold.it/100x100" alt="" class="info-head">
        <p class="info-name">渠道商/合作商名字</p>
        <p class="info-id">ID: 1234567
          <router-link
          :to="{
          path: '/service-account'
          }">
            <img src="../../assects/images/channel_order_information.png" class="go-info-icon">
          </router-link>
        </p>
      </div>

      <!--订单基本统计结果-->
      <div class="order-rude">
        <div class="rude-head">
          <img src="../../assects/images/channel_recycling_configuration.svg" alt="" class="head-icon">
          <span>回收配置</span>
          <span class="service-info">中山大学·线上服务点·待启用</span>
          <img src="../../assects/images/choose_more.svg" class="service-detail">
        </div>
        <div class="rude-num">
          <div class="num-left">
            <img src="../../assects/images/order_total.svg">
            <div class="order-num">
              <p>886</p>
              <h3>总订单数</h3>
            </div>
          </div>
          <div class="num-right">
            <img src="../../assects/images/channel_order_effective.svg">
            <div class="order-num">
              <p>86</p>
              <h3>今日有效订单</h3>
            </div>
          </div>
        </div>
      </div>

      <!--可视化数据图-->
      <div class="order-chart">
        <ul class="tab-list">
          <li class="tab-item"
              :class="{tabActive: activeIndex === 'D'}"
              @click="emitTab('D')">日
          </li>
          <li class="tab-item"
              :class="{tabActive: activeIndex === 'W'}"
              @click="emitTab('W')">周
          </li>
          <li class="tab-item"
              :class="{tabActive: activeIndex === 'M'}"
              @click="emitTab('M')">月
          </li>
        </ul>
        <chart :options="option"></chart>
        <p class="point-detail"> {{ echartTitle }}</p>
        <p class="hint">左右滑动查看更多、点击空点查看</p>
      </div>

      <!--二维码名片  备注：脱离文档流-->
      <div
        v-if="isShowCode"
        @click="changeCodeShowStatus"
        class="QR-code-wrap">
        <div class="QR-code">
          <img src="http://placehold.it/100x100" alt="" class="head-img">
          <p class="user-name">渠道商/合作商名称</p>
          <p class="user-id">ID：4455890</p>
          <img src="http://placehold.it/400x400" alt="" class="code-img">
          <p class="scan-hint">扫一扫上面的二维码图案</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      let self = this
      return {
        activeIndex: 'D',
        echartTitle: '',
        isShowCode: false,
        option: {
          animationDuration: 1000,
          dataZoom: [
            {
              type: 'inside',
              start: 60,
              end: 100
            }
          ],
          xAxis: {
            axisPointer: {
              lineStyle: {
                color: '#00EE00',
                opacity: 0.5,
                width: 2
              },
            },
            data: ['2/1', '2/2', '2/3', '2/4', '2/5', '2/6', '2/7', '2/8', '2/9', '2/1', '2/2', '2/3', '2/4', '2/5', '2/6', '2/7', '2/8', '2/9']
          },
          yAxis: {
            splitLine: {
              show: false
            }
          },
          tooltip: {
            trigger: 'axis',
            formatter: function (params, ticket, callback) {
              self.echartTitle = params[0].axisValue + '订单数为' + params[0].value
              callback()
            }
          },
          series: [{
            name: '订单数',
            type: 'line',
            symbolSize: 10, // 拐点大小
            areaStyle: {
              color: 'rgba(54, 173, 0, 0.1)'
            },
            itemStyle: {
              normal: {
                color: '#00EE00', // 拐点颜色
                label: { // 标签文字
                  // backgroundColor: '#123234',
                  show: true

                },
                lineStyle: { // 折线样式
                  color: '#00EE00',
                  width: 2
                }
              }
            },
            data: [5, 20, 36, 18, 10, 25, 10, 13, 25, 5, 20, 36, 40, 7, 30, 20, 18, 13]
          }]
        }
      }
    },
    methods: {
      emitTab: function (type) {
        let self = this
        self.activeIndex = type
      },
      // 是否展示二维码名片
      changeCodeShowStatus: function () {
        let self = this
        self.isShowCode = !self.isShowCode
      }
    }
  }
</script>
<style lang="less" scoped>
  .service-home {
    /*头部通知栏*/
    .callboard {
      height: 28px;
      overflow: hidden;
      background: #FFF3DC;
      .notice-icon {
        float: left;
        margin-top: 7px;
        padding: 0 5px 0 5px;
        height: 14px;
      }
      .info-list {
        .info-item {
          font-size: 14px;
          color: #E17100;
          line-height: 28px;
          overflow:hidden;
          text-overflow:ellipsis;
          white-space:nowrap;
        }
      }
    }
    /*渠道商简介*/
    .partner-info {
      position: relative;
      padding: 25px 0 25px 0;
      text-align: center;
      background: url("../../assects/images/info-background.jpg") no-repeat;
      background-size: 100% 100%;
      .code-icon {
        position: absolute;
        width: 20px;
        left: 20px;
      }
      .news-notice {
        display: inline-block;
        position: absolute;
        right: 30px;
        top: 10px;
        img {
          padding-top: 10px;
          width: 25px;
          height: 38px;
        }
        .news-num {
          position: absolute;
          right: -40%;
          top: 0;
          min-width: 19px;
          height: 19px;
          padding: 2px;
          text-align: center;
          font-size: 11px;
          color: #FFFFFF;
          line-height: 14px;
          background: #FF4848;
          border-radius: 50%;
        }
      }
      .info-head {
        width: 50px;
        height: 50px;
        box-shadow: 0 0 3px 3px white;
        border-radius: 50%;
      }
      .info-name {
        font-size: 16px;
        color: #FFFFFF;
        line-height: 2;
      }
      .info-id {
        font-size: 13px;
        color: #FFFFFF;
        line-height: 2;
        .go-info-icon {
          height: 13px;
          margin-top: -3px;
        }
      }
    }
    /*总体数据*/
    .order-rude {
      width: 90vw;
      min-height: 20vh;
      margin: -22px auto 0 auto;
      position: relative;
      z-index: 2;  // 置于简介浪上面
      background: #FFFFFF;
      box-shadow: 0 0 20px 0 rgba(18,21,58,0.18);
      border-radius: 12px;
      .rude-head {
        position: relative;
        padding-left: 15px;
        margin-top: 15px;
        font-size: 16px;
        color: #191D21;
        line-height: 3;
        border-bottom: 1px solid #E9ECF5;;
        .head-icon {
          height: 16px;
        }
        .service-info {
          font-size: 12px;
          float: right;
          margin-right: 35px;
          margin-top: 6px;
        }
        .service-detail {
          height: 18px;
          position: absolute;
          right: 15px;
          top: 16px;
        }
      }
      .rude-num {
        height: 14vh;
        display: flex;
        flex-direction: row;
        align-items: center;
        .num-left {
          width: 50%;
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          border-right: 1px solid #E9ECF5;
          img {
            width: 30px;
            margin-right: 10px;
          }
          .order-num {
            p {
              font-size: 22px;
              color: #191D21;
              line-height: 22px;
            }
            h3 {
              font-size: 13px;
              color: #9D9DA5;
              line-height: 13px;
            }
          }
        }
        .num-right {
          width: 50%;
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          img {
            width: 30px;
            margin-right: 10px;
          }
          .order-num {
            p {
              font-size: 22px;
              color: #191D21;
              line-height: 22px;
            }
            h3 {
              font-size: 13px;
              color: #9D9DA5;
              line-height: 13px;
            }
          }
        }
      }
    }
    /*折线图*/
    .order-chart {
      width: 90vw;
      min-height: 40vh;
      margin: 20px auto;
      background: #FFFFFF;
      box-shadow: 0 0 20px 0 rgba(18, 21, 58, 0.18);
      border-radius: 12px;
      .tab-list {
        display: flex;
        flex-direction: row;
        padding: 0 20px;
        .tab-item {
          flex: 1;
          text-align: center;
          font-size: 16px;
          color: #191D21;
          line-height: 3;
          margin: 0 20px;
        }
        .tabActive {
          color: #FFB717;
          border-bottom: 4px solid #FFB717;
        }
      }
      .echarts {
        display: block;
        width: 90%;
        margin: -20px auto 0 auto;
        height: 40vh;
      }
      .point-detail {
        margin-top: -20px;
        text-align: center;
        font-size: 14px;
        color: #0D000F;
        line-height: 1;
      }
      .hint {
        text-align: center;
        color: #FFB717;
        font-size: 12px;
        line-height: 2;
      }
    }
    /*二维码名片*/
    .QR-code-wrap {
      position: fixed;
      width: 100vw;
      height: 100vh;
      top: 0;
      z-index: 10;  // 置于最上面
      background: rgba(1, 1, 1, 0.6);
      .QR-code {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 85vw;
        min-height: 58vh;
        transform: translate(-50%, -50%);
        background: white;
        border-radius: 4px;
        text-align: center;
        .head-img {
          width: 50px;
          height: 50px;
          margin-top: 30px;
          margin-bottom: 15px;
          border-radius: 50%;
        }
        .user-name {
          font-size: 16px;
          color: #191D21;
          line-height: 16px;
        }
        .user-id {
          font-size: 13px;
          color: #9D9DA5;
        }
        .code-img {
          width: 53vw;
          height: 53vw;
          margin: 20px auto;
        }
        .scan-hint {
          font-size: 13px;
          color: #9D9DA5;
          margin-bottom: 20px;
        }
      }
    }
    /*元素级样式*/
  }
</style>



